import styled from "styled-components";

import { Title3 } from "../../assets/styles/component";
import Service from "./serviceDesktop";

import Full_service from "../../assets/images/Elements/Full_service.webp";
import Full_service_1 from "../../assets/images/Elements/full-service_1.png";
import Full_service_2 from "../../assets/images/Elements/full-service_2.png";
import Full_service_3 from "../../assets/images/Elements/full-service_3.png";
import Full_service_4 from "../../assets/images/Elements/full-service_4.png";
import Full_service_5 from "../../assets/images/Elements/full-service_5.png";
import Full_service_6 from "../../assets/images/Elements/full-service_6.png";
import Full_service_7 from "../../assets/images/Elements/full-service_7.png";
import Self_service_1 from "../../assets/images/Elements/Self_service_1.webp";
import Self_service_2 from "../../assets/images/Elements/Self_service_2.webp";

import { EngLoc } from "../../assets/constants/localizations";

const SWork = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 6.25rem;

  margin: 0 8.3%;
  padding: 190px 0;
  max-width: 100rem;

  @media (min-width: 3000px) {
    margin: 0 auto;
  }

  @media (max-width: 1200px) {
    width: calc(100% - 10%);
    margin: auto;
  }
`;

const TitleBox = styled.div`
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  display: flex;
  flex-direction: column;
  gap: 6rem;

  text-align: left;
`;

const Title = styled(Title3)`
  font-size: clamp(45px, 8vw, 96px);
  font-weight: 400;
  line-height: 110%;
`;

const Services = styled.div`
  display: flex;
  justify-content: center;

  gap: 1.25rem;

  @media (min-width: 2500px) {
    gap: 5.6rem;
  }

  @media (max-width: 1200px) {
    flex-wrap: wrap;
  }
`;

const Work = ({ language }) => {
  return (
    <SWork id={"services"}>
      <TitleBox>
        <Title weight={300}>
          {language === "Ru" ? "Как мы работаем" : EngLoc.services.title}
        </Title>
      </TitleBox>
      <Services>
        <Service
          language={language}
          title={
            language === "Ru"
              ? "Full-сервис"
              : EngLoc.services.services_list[0].title
          }
          desc={
            language === "Ru"
              ? `Наша команда поможет с решением любых задач с решением под ключ. 
              35 специалистов в штате аккаунтинга и реализации помогут 
              с достижением ваших целей в заданном бюджете и в нужный срок.`
              : EngLoc.services.services_list[0].desc
          }
          imgs={[
            { img: [Full_service_1], href: "1", width: "113", height: "113" },
            { img: [Full_service_2], href: "2", width: "113", height: "113" },
            { img: [Full_service_3], href: "2", width: "113", height: "113" },
            { img: [Full_service_4], href: "3", width: "113", height: "113" },
            { img: [Full_service_5], href: "4", width: "113", height: "113" },
            { img: [Full_service_6], href: "5", width: "113", height: "113" },
            { img: [Full_service_7], href: "6", width: "113", height: "113" },
          ]}
          btnText={"Заполнить бриф"}
        />
        <Service
          language={language}
          title={
            language === "Ru"
              ? "Self-сервис"
              : EngLoc.services.services_list[1].title
          }
          desc={
            language === "Ru"
              ? `Автоматизация всей рутины при размещении нативной рекламы в сообществах, 
              каналах и на сайтах с видео-контентом в удобном интерфейсе рекламных кабинетов.`
              : EngLoc.services.services_list[1].desc
          }
          btnText={"Зарегистрироваться"}
          imgs={[
            { img: Self_service_1, width: "336", height: "244" },
            { img: Self_service_2, width: "336", height: "244" },
          ]}
        />
      </Services>
    </SWork>
  );
};

export default Work;
