import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";

const SService = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.938rem;
  padding: 3.063rem;
  width: 50%;
  max-width: 600px;

  &:not(:first-child) {
    overflow: hidden;
  }

  background: linear-gradient(90.7deg, #f4faff 8.32%, #fbfdff 99.04%), #0079e7;
  border-radius: 2.75rem;

  & button {
    background: radial-gradient(
      105.43% 294.48% at -10.2% -116.67%,
      #376ce0 0%,
      #2485dd 25.96%,
      #4f4ed9 74.99%,
      #7320cf 96.87%
    );
    box-shadow: inset 0 0 37px -11px rgba(255, 255, 255, 0.29);
    border-radius: 34.5px;
  }

  &:first-child {
    & > div {
      gap: 0;
      justify-content: flex-start;
    }
  }

  @media (max-width: 1200px) {
    width: 100%;
  }
`;

const Images = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 244px;
  gap: 1.25rem;

  & > div {
    position: relative;
    width: 113px;
    height: 113px;

    &:not(:first-child) {
      margin-left: -35px;

      @media (max-width: 1400px) and (min-width: 1200px) {
        margin-left: -65px;
      }
    }

    img {
      margin-top: 0;
      border-radius: 200px;
      outline: 0px solid #fff;
      transition: 0.15s ease-in-out;
    }

    &:hover:not(:last-child) {
      img {
        outline: 4px solid #fff;
        margin-top: -15px;
      }

      div {
        transform: scale(1.0) translateY(calc(-100% - 35px));
      }
    }
  }
`;

const Title = styled(Title3)`
  display: flex;
  gap: 1.5rem;

  font-size: clamp(39px, 4vw, 54px);
  font-weight: 400;
  line-height: 130%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  margin-top: 4.688rem;
`;

const SText = styled(Text)`
  margin-bottom: 2.188rem;
`;

const Img = styled.img`
  width: 100%;
  opacity: 0;

  ${({ view, index }) =>
    !view &&
    `
      animation: 1s 0.${index}s ease-in-out smooth_left forwards;

      @keyframes smooth_left {
        0% {
          opacity: 0;
          transform: translateX(-100px);
        }
        100% {
          opacity: 1;
          transform: translateX(0);
        }
      }
    `}
`;

const Support = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 16px 12px 16px 19px;
  top: 0;
  left: -15px;
  min-width: 380px;
  z-index: 100;
  
  transform: scale(0);
  transition: all 0.25s ease-in-out;
  background: #ffffff;
  border-radius: 20px;

  p {
    width: 100%;
    color: #3c3c3c;
    font-size: 16px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 60px;
    bottom: -25px;
    border: 15px solid transparent;
    border-top: 15px solid #fff;
  }
`;

const Telegram = styled.a`
  display: flex;
  gap: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #0b85da;
`;

const Service = ({ title, desc, btnText, imgs }) => {
  const [view, setView] = useState(true);
  const elem = useRef(null);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  return (
    <SService>
      <Images>
        {imgs.map((img, index) =>
          img.href ? (
            <div key={index}>
              <Support>
                <Text>
                  Привет, готов ответить на твои вопросы прямо сейчас в
                  телеграм:
                </Text>
                <Telegram>
                  <svg
                    width="20"
                    height="20"
                    viewBox="0 0 20 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M20 9.99997C20 15.5225 15.5225 20 10 20C4.4775 20 0 15.5225 0 9.99997C0 4.47747 4.4775 -3.05176e-05 10 -3.05176e-05C15.5225 -3.05176e-05 20 4.47747 20 9.99997ZM10.3583 7.38247C9.38583 7.78664 7.44167 8.62414 4.52667 9.89414C4.05333 10.0825 3.805 10.2666 3.7825 10.4466C3.74417 10.7516 4.12583 10.8716 4.64417 11.0341C4.715 11.0566 4.78833 11.0791 4.86333 11.1041C5.37417 11.27 6.06083 11.4641 6.4175 11.4716C6.74167 11.4783 7.10333 11.345 7.5025 11.0716C10.2258 9.23247 11.6317 8.3033 11.72 8.2833C11.7825 8.26914 11.8692 8.2508 11.9275 8.3033C11.9858 8.35497 11.98 8.4533 11.9742 8.47997C11.9358 8.6408 10.4408 10.0316 9.66583 10.7516C9.42417 10.9758 9.25333 11.135 9.21833 11.1716C9.14 11.2525 9.06 11.33 8.98333 11.4041C8.50833 11.8608 8.15333 12.2041 9.00333 12.7641C9.41167 13.0333 9.73833 13.2558 10.0642 13.4775C10.42 13.72 10.775 13.9616 11.235 14.2633C11.3517 14.34 11.4633 14.4191 11.5725 14.4966C11.9867 14.7925 12.3592 15.0575 12.8192 15.0158C13.0858 14.9908 13.3625 14.74 13.5025 13.9908C13.8333 12.2191 14.485 8.38247 14.6358 6.8008C14.645 6.66948 14.6394 6.53754 14.6192 6.40747C14.607 6.30241 14.5559 6.20575 14.4758 6.13664C14.3567 6.03914 14.1717 6.0183 14.0883 6.01997C13.7125 6.02664 13.1358 6.22747 10.3583 7.38247Z"
                      fill="#0B85DA"
                    />
                  </svg>
                  {img.href}
                </Telegram>
              </Support>
              <Img
                ref={elem}
                view={view}
                index={index}
                key={index}
                width={img.width}
                height={img.height}
                loading="lazy"
                alt="Service_img"
                src={img.img}
              />
            </div>
          ) : (
            <Img
              ref={elem}
              view={view}
              index={index}
              key={index}
              width={img.width}
              height={img.height}
              loading="lazy"
              alt="Service_img"
              src={img.img}
            />
          )
        )}
      </Images>
      <Title>{title}</Title>
      <SText size="20" color="#3C3C3C;">
        {desc}
      </SText>
      <Button type="service" colorTxt="#E7F5FF" link={true}>
        {btnText}
      </Button>
    </SService>
  );
};

export default Service;
